import React from 'react';
import axios from "axios";
import Search from "./Search";
import MyNavbar from "../../components/MyNavbar";
import Footer from "../../components/Footer";
import moment from 'moment'

class Teratail extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      postsList: [],
    };

    this.handleClick = this.handleClick.bind(this);
    this.renderImageList = this.renderImageList.bind(this);
  }

  handleClick(target) {
    const limit = 40;
    const url = `https://teratail.com/api/v1/tags/react.js/questions?page=1&limit=${limit}`;
    axios
      .get(url)
      .then((res) => {
        this.setState({ postsList: res.data.questions });
    console.log('hello');
    console.log(res.data);
      })
      .catch(console.error);
  }

  renderImageList(list) {
    const posts = list.map((item, index) => {
      const user = item.user
      if(user == null) {
        const profileImage2 = 'https://teratail-v2.storage.googleapis.com/uploads/avatars/u8/86365/UYo8ovxu_thumbnail.jpg'
        return (
          <li className="item" key={index}>
            <img class="css-100alwu eyfquo10" src={profileImage2} width="40" height="40" loading="lazy" />
            <a href={item.id} target="_blank" rel="noreferrer">{item.title}</a> {moment(item.created).fromNow()} 回答数:{item.count_reply} PV数:{item.count_pv}
          </li>
        );
      }
      else {
        const url = "https://teratail.com/questions/" + item.id
        return (
          <li className="item" key={index}>
            <img class="css-100alwu eyfquo10" src={item.user.photo} width="40" height="40" loading="lazy" />
            <a href={url} target="_blank" rel="noreferrer">{item.title}</a> {moment(item.created).fromNow()} 回答数:{item.count_reply} PV数:{item.count_pv}
          </li>
        );
      }
    });
    return posts;
  }

  render() {
    return (
      <div>
        <MyNavbar />
        <Search search={this.handleClick} />
        <ul>{this.renderImageList(this.state.postsList)}</ul>
        <Footer />
      </div>
    );
  }
}

export default Teratail;
